import "./List.css";
import React, { useState, useEffect } from "react";
import axios from "axios";
import { Table, Button, Popconfirm } from "antd";

const columns = [
  {
    title: "Short",
    dataIndex: "short",
    render: (short) => <a href={window.location.href + short}>{short}</a>,
  },
  {
    title: "Full",
    dataIndex: "full",
    render: (full) => <a href={full}>{full}</a>,
  },
  {
    title: "Clicks",
    dataIndex: "clicks",
  },
  {
    title: "Protected",
    dataIndex: "password",
    render: (password) => (password ? <p>&#x1F512;</p> : <p>&#x1F513;</p>),
  },
];

const List = ({ username, idToken, mobileMode, shortURL, setError }) => {
  const [urls, setUrls] = useState([
    {
      key: "asdasd",
      short: "John Brow asdasdas asd asn",
      full: "sadfjhasdgaskdgkasduiasdgasdyhasidhasidhasi",
      clicks: 10,
    },
  ]);
  const [refreshUrls, setRefreshUrls] = useState(false);
  const [count, setCount] = useState(100);
  const [selectedRows, setSelectedRows] = useState([]);
  const [loading, setLoading] = useState(false);
  const [pageNumber, SetPageNumber] = React.useState(1);

  useEffect(() => {
    axios
      .post("/api", { idToken, username, pageNumber })
      .then((res) => {
        let finalList = [];
        res.data.shortUrls.forEach(({ short, full, clicks, _id, password }) => {
          finalList.push({
            short,
            full,
            clicks,
            key: _id,
            password: password ? true : false,
          });
        });
        setUrls(finalList);
        axios
          .post("/api/shortUrls/count", { idToken, username })
          .then(({ data: { count } }) => setCount(count))
          .catch((err) => {
            console.log(err);
            setError("Something went wrong");
          });
      })
      .catch((err) => {
        console.log(err);
        setError("Something went wrong");
      });
  }, [username, refreshUrls, idToken, setError, pageNumber]);

  useEffect(() => {
    setRefreshUrls((prev) => !prev);
  }, [shortURL]);

  const deleteUrlsFromList = () => {
    setLoading(true);
    const ids = [];
    selectedRows.forEach((x) => ids.push(urls[x].key));
    axios
      .delete("/api/shortUrls", { data: { idToken, username, ids } })
      .then(() => {
        setRefreshUrls((prev) => !prev);
        setLoading(false);
        SetPageNumber(1);
      })
      .catch((err) => {
        setLoading(false);
        console.log(err);
        setError("Something went wrong");
      });
  };

  const rowSelection = {
    selectedRows,
    onChange: (selectedRowKeys) => setSelectedRows(selectedRowKeys),
  };
  const hasSelected = selectedRows.length > 0;

  return (
    <div className="list-container">
      <p>Table of links generated</p>
      <div style={{ marginBottom: 16 }}>
        <Popconfirm
          title="Are you sure to delete selected URLs?"
          onCancel={deleteUrlsFromList}
          okText="Cancel"
          cancelText="Delete"
        >
          <Button type="danger" disabled={!hasSelected} loading={loading}>
            Delete
          </Button>
        </Popconfirm>
        <span style={{ marginLeft: 8 }}>
          {hasSelected ? `Selected ${selectedRows.length} items` : ""}
        </span>
      </div>
      <Table
        rowSelection={rowSelection}
        columns={columns}
        dataSource={urls}
        scroll={{ x: 1000 }}
        pagination={{
          total: count,
          showSizeChanger: false,
          onChange: (page) => SetPageNumber(page),
        }}
      />
    </div>
  );
};

export default List;
